* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.w-100 {
    width: 100%;
}

.max-height-100 {
    max-height: calc(100vh - 150px);
    overflow: auto;
}

.empty-slot {
    padding: 1rem;
    margin: 1rem;
    color: #333;
    background: #eee;
    border-radius: 8px;
    margin-bottom: 0.5rem;
}

.movies-list-wrapper {
    display: flex;
}

.section-title {
    margin: 1rem;
    font-size: 1rem;
}

.movie-list {
    margin: 1rem;
}

.movies-search {
    padding: 1rem;
}

input {
    padding: .5rem;
    border-radius: 4px;
    border: 1px solid #666;
}

.movie {
    padding: 1rem;
    color: #333;
    background: #eee;
    border-radius: 8px;
    margin-bottom: 0.5rem;
    width: 99%;
}

.movie-title {
    font-size: 1.2rem;
}

.movie-info {
    font-style: italic;
}

button {
    position: relative;
    padding: 0.3rem 1.2rem;
    border-radius: 3px;
    border: 1px solid #222;
    cursor: pointer;
    margin-top: .5rem;
}

button:hover::after {
    opacity: 1;
    width: 100%;
}

button::after {
    content: "";
    position: absolute;
    z-index: 1;
    width: 0;
    height: 100%;
    background: rgba($color: #000000, $alpha: 0.125);
    top: 0;
    left: 0;
    opacity: 0;
    transition: all 0.5s ease-out;
}

button.success {
    background: rgb(103, 235, 235);
}

button.danger {
    background: rgb(255, 151, 151);
}